.list-group-item {
  padding: 15px 20px;
  border-bottom: 1px solid #3d5a80;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.list-group-item:last-child {
  border-bottom: none;
}

.name {
  flex: 1;
  line-height: 35px;
  font-size: 20px;
  cursor: pointer;
}

.salary {
  margin-right: 125px;
  line-height: 35px;
  font-size: 20px;
}

.list-group-item.increase .name,
.list-group-item.increase .salary {
  color: #e09f3e;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}

.button {
  width: 35px;
  height: 35px;
  margin: 3px;
  font-size: 17px;
  border: none;
  cursor: pointer;
  text-align: center;
}

.сookieColor {
  color: #e09f3e;
}

.trashColor {
  color: #e5383b;
}

.fa-star {
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  font-size: 16px;
  color: #ffd700;
  transition: 0.3s all;
  transform: translateX(30px);
  opacity: 0;
}
.like .fa-star {
  opacity: 1;
  transform: translateX(0px);
}

@media (max-width: 750px) {
  .salary {
    margin-right: 50px;
  }
}

@media (max-width: 475px) {
  .list-group-item {
    flex-wrap: wrap;
  }

  .name {
    margin-bottom: 10px;
    flex: 0 0 100%;
  }

  .salary {
    flex: 0 0 auto;
  }

  .buttons {
    justify-content: flex-end;
    flex: 1;
  }
}
